<template>
    <main-layout
        :loading="dataLoading"
        :settings-breadcrumbs="settingsBreadcrumbs"
        :classes="'page_account-friends'"
    >
        <template #breadcrumps>
            <div class="bc-wrapper_row">
                <div class="bc-wrapper_row-btns">
                    <router-link
                        :to="{ name: 'new_friend' }"
                        class="btn-base btn-base_normal btn-base_border"
                    >
                        <account-plus-outline />
                        Add Friend
                    </router-link>
                    <!--<router-link-->
                    <!--    to="as"-->
                    <!--    class="btn-base btn-base_normal btn-base_colored"-->
                    <!--&gt;-->
                    <!--    <play-circle-outline />-->
                    <!--    Watch video-->
                    <!--</router-link>-->
                </div>

                <div class="bc-wrapper_row-details">
                    <div class="bc-wrapper_row-details_item">
                        <div class="bc-wrapper_row-details_item-icon">
                            <img
                                src="@/assets/img/svg/writing.svg"
                                alt=""
                            >
                        </div>
                        <div class="bc-wrapper_row-details_item-text">
                            <span class="number">{{ countOrders }}</span>
                            <span class="text">Orders</span>
                        </div>
                    </div>
                    <div class="bc-wrapper_row-details_item">
                        <div class="bc-wrapper_row-details_item-icon">
                            <img
                                src="@/assets/img/svg/walet.svg"
                                alt=""
                            >
                        </div>
                        <div class="bc-wrapper_row-details_item-text">
                            <span class="number earnings">${{ sumEarninngs }}</span>
                            <span class="text">Earnings</span>
                        </div>
                    </div>
                </div>
            </div>
        </template>
        <template #content>
            <div class="friends">
                <div class="wrapper">
                    <div class="friends_sort">
                        <span class="friends_sort-text">Sort by:</span>
                        <ul class="friends_sort-menu">
                            <li
                                v-for="(val, id) in sortMenu"
                                :key="id"
                                class="friends_sort-menu-item"
                                :class="apiSetting.sort_by == val.sort_by ? 'friends_sort-menu-item--active' : ''"
                                @click="changeFilter(val.sort_by)"
                            >
                                {{ val.text }}
                                <span
                                    class="filter-bar__sort-order"
                                    :class="apiSetting.sort_order === 'DESC' ? 'friends_sort-menu-arrow--top' : ''"
                                ></span>
                            </li>
                        </ul>
                    </div>

                    <div
                        v-if="registerFriends.length > 0"
                        class="friends_list"
                    >
                        <router-link
                            v-for="(friend, index) in registerFriends"
                            :key="index"
                            :to="{ name: 'friend_detail', params: { id: friend.id }}"
                            class="friends_list_card"
                        >
                            <div class="friends_list_card-left">
                                <div class="friends_list_card-left__user">
                                    <span
                                        v-if="friend.country"
                                        class="friends_list_card-left__user__flag"
                                        :class="`review__flag flag-icon flag-icon-` + friend.country.toLowerCase()"
                                    />
                                    <div class="friends_list_card-left__user-info">
                                        <router-link
                                            :to="{ name: 'friend_detail', params: { id: friend.id }}"
                                            class="friends-card__name"
                                        >
                                            {{ `${friend.lastname}, ${friend.firstname} ` }}
                                        </router-link>

                                        <span class="friends-card__email">{{ friend.email }}</span>
                                    </div>
                                </div>
                            </div>
                            <div class="friends_list_card-right friends_list_card-right-register">
                                <!-- total credits -->
                                <div class="friends-card__credits">
                                    {{ friend.total_earnings | money }}
                                    <br>
                                    <span>purchases</span>
                                </div>
                                <!-- total credits -->

                                <!-- total orders -->
                                <div class="friends-card__orders">
                                    {{ friend.batches.length }}
                                    <br><span>orders</span>
                                </div>
                                <!-- total orders -->
                                <p class="friends_list_card-right__date">
                                    {{ friend.created_at | moment_bidding_text }}
                                </p>
                                <p class="friends_list_card-right__status">
                                    {{ friend.status.toLowerCase() }}
                                </p>
                            </div>
                        </router-link>
                        <router-link
                            v-for="friend in invitedFriends"
                            :key="friend.id"
                            :to="{ name: 'friend_detail', params: { id: friend.id }}"
                            class="friends_list_card"
                        >
                            <div class="friends_list_card-left">
                                <div class="friends_list_card__avatar">
                                    <avatar
                                        :avatar="friend.avatar"
                                        :firstname="friend.firstname"
                                        :lastname="friend.lastname"
                                        :email="friend.email"
                                    />
                                </div>
                                <p class="friends_list_card-left__email">
                                    {{ friend.email }}
                                </p>
                            </div>
                            <div class="friends_list_card-right">
                                <p class="friends_list_card-right__date">
                                    {{ friend.created_at | moment_bidding_text }}
                                </p>
                                <p class="friends_list_card-right__status">
                                    {{ friend.status.toLowerCase() }}
                                </p>
                            </div>
                        </router-link>
                        <custom-button
                            v-if="friends.next_page_url"
                            default
                            class="button btn-base_colored sort-button"
                            @on-btn-click="loadMore"
                        >
                            Load more
                        </custom-button>
                    </div>
                    <no-result
                        v-else
                        :type="'friends'"
                    />
                </div>
            </div>
        </template>
    </main-layout>
</template>

<script>
import Api from '@/helpers/api/index.js';
import filtersMixin from '@/mixins/filtersMixin.js'
// import PlayCircleOutline from 'mdi-vue/PlayCircleOutline';
import AccountPlusOutline from 'mdi-vue/AccountPlusOutline';

import MainLayout from '@/layouts/MainLayout';
import NoResult from '@/components/common/NoResult';
import Avatar from '@/components/common/Avatar';

export default {
    components: {
        MainLayout,
        NoResult,
        Avatar,
        // PlayCircleOutline,
        AccountPlusOutline
    },
    mixins: [filtersMixin],
    data() {
        return {
            apiSetting: {
                page: 1,
                sort_by: 'lastname',
                sort_order: 'DESC',
                per_page: 5
            },
            lastPage: 1,
            sortMenu: [
                { text: 'name', sort_by: 'lastname' },
                { text: 'date', sort_by: 'created_at' },
                { text: 'credits', sort_by: 'total' }
            ],
            friends: {},
            dataLoading: true
        }
    },
    computed: {
        settingsBreadcrumbs() {
            return {
                title: 'My Friends Affiliate Program',
                steps: [
                    {
                        title: 'Home',
                        url: '/'
                    },
                    {
                        title: 'Account',
                        url: '/cp/account'
                    },
                    {
                        title: 'My Friends',
                        url: '/cp/friends'
                    }
                ],
                excerpt: 'Your account will be automatically credited with 10% of the purchase price every time your friends make an order. Then, the amounts will appear in your credits ledger. You can use the balance towards your own purchases, or cash it out.',
                additionalClass: 'bc-horizontal bc-wrapper_friends'
            }
        },
        invitedFriends() {
            if (this.friends.data) {
                const allData = this.friends.data
                const invitedArray = allData.filter((allData) => allData.status === 'INVITED')
                return invitedArray
            }
            return []
        },
        registerFriends() {
            if (this.friends.data) {
                const allData = this.friends.data
                const invitedArray = allData.filter((allData) => allData.status === 'REGISTERED')
                return invitedArray
            }
            return []
        },
        countOrders() {
            if (this.friends.data.length) {
                return this.friends.data.reduce((sum, current) => sum + current.batches.length, 0);
            }
            return 0
        },
        sumEarninngs() {
            if (this.friends.data.length) {
                return this.friends.data.reduce((sum, current) => sum + current.total_earnings, 0);
            }
            return 0
        }
    },
    async mounted() {
        await this.getData();
    },
    methods: {
        loadMore() {
            this.lastPage += 1
            this.apiSetting.page = this.lastPage
            this.apiSetting.per_page = 5
            Api.post('/api/cp/client-friend/list-friends', this.apiSetting)
                .then((response) => {
                    const data = [...this.friends.data, ...response.data.data]
                    this.friends = response.data
                    this.friends.data = [...data]
                })
                .catch(() => {
                })
                .finally(() => {
                    this.dataLoading = false
                })
        },
        changeFilter(str) {
            this.apiSetting.per_page = this.friends.data.length
            if (this.apiSetting.page !== 1) this.lastPage = this.apiSetting.page
            this.apiSetting.page = 1
            this.apiSetting.sort_order = this.apiSetting.sort_order === 'ASC' ? 'DESC' : 'ASC'
            this.apiSetting.sort_by = str
            this.getData()
        },
        getData() {
            const vm = this;
            Api.post('/api/cp/client-friend/list-friends', this.apiSetting)
                .then((response) => {
                    vm.friends = response.data
                })
                .catch(() => {

                })
                .finally(() => {
                    vm.dataLoading = false
                })
        }
    }
}
</script>

