<template>
    <main-layout
        :settings-breadcrumbs="settingsBreadcrumbs"
    >
        <template #breadcrumps>
            <div class="bc-wrapper_row">
                <div class="friend-detail__info">
                    <div class="friend-detail__info-img">
                        <img
                            v-if="friend.country"
                            :src="require('@/assets/img/flags/4x3/' + friend.country.toLowerCase() + '.svg')"
                            alt=""
                        >
                    </div>
                    <div class="friend-detail__info-about">
                        <h5 class="friend-detail__info-about__fullname">
                            {{ friend.firstname }} {{ friend.lastname }}
                        </h5>
                        <span class="friend-detail__info-about__email">{{ friend.email }}</span>
                    </div>
                </div>
                <div class="bc-wrapper_row-details">
                    <div class="bc-wrapper_row-details_item">
                        <div class="bc-wrapper_row-details_item-icon">
                            <img
                                src="@/assets/img/svg/writing.svg"
                                alt=""
                            >
                        </div>
                        <div class="bc-wrapper_row-details_item-text">
                            <span class="number">{{ batches.length }}</span>
                            <span class="text">Orders</span>
                        </div>
                    </div>
                    <div class="bc-wrapper_row-details_item">
                        <div class="bc-wrapper_row-details_item-icon">
                            <img
                                src="@/assets/img/svg/walet.svg"
                                alt=""
                            >
                        </div>
                        <div class="bc-wrapper_row-details_item-text">
                            <span class="number earnings">${{ friend.total_earnings }}</span>
                            <span class="text">Earnings</span>
                        </div>
                    </div>
                </div>
            </div>
        </template>
        <template #content>
            <no-result
                v-if="!batches.length"
                :type="'friend'"
            />
            <div v-else>
                <div
                    v-if="batches.length > 0 && !dataLoading"
                    class="friend-detail__content"
                >
                    <div class="friend-detail__list">
                        <div
                            v-for="item in batches"
                            :key="item.id"
                            class="friend-detail__list-item"
                        >
                            <div class="friend-detail__list-item__details">
                                <div class="friend-detail__list-item__details-item">
                                    <div class="friend-detail__list-item__details-item__title">
                                        Batch ID
                                    </div>
                                    <div class="friend-detail__list-item__details-item__val">
                                        <board-check />
                                        {{ item.batchid }}
                                    </div>
                                </div>
                                <div class="friend-detail__list-item__details-item friend-detail__list-item__details-item__start">
                                    <div class="friend-detail__list-item__details-item__title">
                                        Ordered: {{ item.created_at_local.date | moment_from }} ago
                                    </div>
                                    <div class="friend-detail__list-item__details-item__val">
                                        <clock />
                                        {{ item.created_at_local.date | moment_short_text }}
                                    </div>
                                </div>
                                <div class="friend-detail__list-item__details-item">
                                    <div class="friend-detail__list-item__status">
                                        {{ item.status }}
                                    </div>
                                </div>
                                <div class="friend-detail__list-item__details-item friend-detail__list-item__details-item__total">
                                    <div class="friend-detail__list-item__details-item__title">
                                        Total
                                    </div>
                                    <div class="friend-detail__list-item__details-item__val">
                                        <img
                                            src="@/assets/img/svg/walet.svg"
                                            alt=""
                                        >
                                        {{ item.total_bd | money }}
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </template>
    </main-layout>
</template>

<script>
import Api from '@/helpers/api/index.js';
import filtersMixin from '@/mixins/filtersMixin.js'
import MainLayout from '@/layouts/MainLayout';
import NoResult from '@/components/common/NoResult';
import Clock from 'mdi-vue/Clock';
import BoardCheck from 'mdi-vue/ClipboardCheckOutline'

export default {
    name: 'FriendDetail',
    components: {
        MainLayout,
        NoResult,
        Clock,
        BoardCheck
    },
    mixins: [
        filtersMixin
    ],
    data() {
        return {
            dataLoading: true,
            friend: {},
            batches: []
        }
    },
    computed: {
        settingsBreadcrumbs() {
            return {
                title: 'Friends profile',
                steps: [
                    {
                        title: 'Home',
                        url: '/'
                    },
                    {
                        title: 'Account',
                        url: '/cp/account'
                    },
                    {
                        title: 'My Friends',
                        url: '/cp/friends'
                    },
                    {
                        title: 'Friend Detail',
                        url: '/cp/friend/:id'
                    }
                ],
                additionalClass: 'bc-wrapper bc-wrapper_user-account bc-wrapper--setting bc-right-bottom bc-horizontal bc-friend-details'
            }
        }
    },
    watch: {
    },
    mounted() {
        this.getData();
    },
    methods: {
        getData() {
            this.dataLoading = true
            Api.get(`/api/cp/client-friend/details/${this.$route.params.id}`)
                .then((response) => {
                    this.friend = response.data;
                    this.batches = response.data.batches
                })
                .catch(() => {
                })
                .finally(
                    this.dataLoading = false
                )
        }
    }
}
</script>

